body {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100vh;
  overflow: auto;
  margin: 0;
  padding: 0;

  font-family: "Ubuntu", "Arial", sans-serif;
  color: $white;
  font-style: normal;
  font-weight: 300;
  font-size: 15px;
  line-height: 1.5;

  background-color: #02050c;
  background-image: url("../img/body-background@1x.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 40%;
  background-attachment: fixed;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/body-background@2x.jpg");
  }
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  position: absolute;

  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;

  white-space: nowrap;

  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.main-background {
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  min-height: 100%;

  &::after {
    content: "";
    position: fixed;
    z-index: -1;
    top: 0;
    left: 0;
    right: 0;
    height: 100vh;

    background-image: url("../img/body-background-main-2@1x.png"), url("../img/body-background-main@1x.jpg");
    background-repeat: no-repeat, no-repeat;
    background-size: cover, cover;
    background-position: 50% 0, 50% 0;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/body-background-main-2@1x.png"), url("../img/body-background-main@2x.jpg");
    }
  }
}
