.questions {
  display: flex;
  flex-direction: column;

  position: relative;

  flex-grow: 1;

  max-width: 1238px;
  width: 100%;
  margin: 0 auto;
  margin-top: 5%;

  padding-left: 27px;
  padding-right: 27px;

  box-sizing: border-box;
  overflow: hidden;

  &::before {
    content: "";
    position: absolute;
    background-image: url("../img/boy.png");
    background-repeat: no-repeat;
    background-size: 235px 264px;
    background-position: 100% 100%;

    width: 235px;
    height: 264px;

    right: -33px;
    bottom: 0;
  }
}

.questions__main {
  flex-grow: 1;
  margin-bottom: 10px;
}

.questions__title {
  @include title;
  margin-bottom: 34px;
}

.questions__list {
  width: 76.5%;
  box-sizing: border-box;
}

.questions__item {
  position: relative;

  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  width: 100%;
  margin-bottom: 5px;
  padding-left: 39px;

  box-sizing: border-box;

  background-color: $white;
  color: $dark-grey;

  cursor: pointer;

  &::before {
    content: "";
    position: absolute;

    width: 9px;
    height: 9px;
    background-color: $green;
    border-radius: 50%;

    top: 31px;
    left: 20px;
  }
}

.questions__question {
  position: relative;

  width: 85%;
  margin: 0;
  padding-top: 19px;
  padding-bottom: 22px;
  box-sizing: border-box;

  font-weight: 500;
  font-size: 21px;
  opacity: 0.8;
}

.questions__answer {
  display: none;

  margin-top: -4px;
  width: 71%;
  padding-left: 11px;
  padding-bottom: 34px;
}

.questions__answer p {
  margin: 0;
}

.questions__item--open .questions__answer {
  display: block;
}

.questions__button {
  position: relative;
  padding: 0;

  width: 68px;

  background-color: $dark-green;
  border: none;
  border-radius: 0;
  box-sizing: border-box;

  outline: none;

  &::before {
    content: "";
    position: absolute;

    width: 24px;
    height: 2px;
    background-color: $white;

    left: 22px;
    top: 50%;
    transform: translateY(-25%);
  }

  &::after {
    content: "";
    position: absolute;

    width: 2px;
    height: 24px;
    background-color: $white;

    left: 33px;
    top: 50%;
    transform: translateY(-50%);
  }
}

.questions__item--open .questions__button {
  &::before {
    content: "";
    position: absolute;

    width: 24px;
    height: 2px;
    background-color: $white;
    transform: rotate(45deg) translateY(-25%);
    left: 22px;
    top: 50%;
  }

  &::after {
    content: "";
    position: absolute;
    background-color: $white;

    transform: rotate(-45deg) translateY(0%);
    left: 22px;
    top: 50%;
    width: 24px;
    height: 2px;
  }
}

@media (max-width: $tablet-width) {
  .questions {
    margin-top: 65px;
    padding-left: 8px;
    padding-right: 8px;

    &::before {
      display: none;
    }
  }

  .questions__list {
    width: 100%;

    box-sizing: border-box;
  }

  .questions__item {
    padding-left: 8px;

    box-sizing: border-box;

    &::before {
      display: none;
    }
  }

  .questions__answer {
    width: 100%;
    margin-top: 8px;
    padding-left: 0;
    padding-right: 16px;
    padding-bottom: 27px;

    box-sizing: border-box;
  }

  .questions__button {
    width: 32px;
    min-height: 44px;

    &::before {
      width: 16px;
      left: 8px;
    }

    &::after {
      height: 16px;
      left: 15px;
    }
  }

  .questions__title {
    margin-left: 8px;
    margin-right: 8px;
    margin-bottom: 27px;
    padding-bottom: 10px;

    line-height: 1.2;
  }

  .questions__question {
    width: 88%;
    padding-top: 9px;
    padding-bottom: 13px;
    line-height: 1.35;
    font-size: 17px;

    box-sizing: border-box;
  }

  .questions__item--open .questions__button {
    width: 32px;

    &::before {
      width: 16px;
      left: 8px;
    }

    &::after {
      width: 16px;
      height: 2px;
      left: 8px;
    }
  }
}
